<template>
    <div class="scroll-container">
        <slot></slot>
    </div>
</template>
<script>

import debounce from "@/utils/debounce";

export default {
    name: "ScrollContainer",
    props: {
        hasMore: {
            type: Boolean,
            required: true
        },
        loadMore: {
            type: Function,
            required: true
        },
        offset: {
            type: Number,
            default: 50
        },
        init:{
            type:Boolean,
            default:false
        }
    },
    data() {
        return {
            list: [],
        }
    },
    mounted() {
        window.addEventListener('scroll', this.windowScroll, true);
        this.$nextTick().then(value => {
            if(this.init) {
                this.loadMore();
            }
        });
    },
    destroyed() {
        //
        window.removeEventListener("scroll", this.windowScroll);
    },
    beforeRouteLeave() {
        //
        window.removeEventListener("scroll", this.windowScroll);
    },
    methods: {
        async windowScroll() {
            let elem = this.$el;
            //
            let clientHeight = Math.min(elem.clientHeight,document.documentElement.clientHeight);
            //
            let scrollTop = elem.scrollTop && elem.scrollTop || document.documentElement && document.documentElement.scrollTop || document.body && document.body.scrollTop;
            let scrollHeight = elem.scrollHeight;
            // console.log(clientHeight, scrollTop, scrollHeight)
            debounce(() => {
                //
                if (clientHeight + scrollTop + this.offset >= scrollHeight && this.hasMore) {
                    //
                    this.loadMore();
                }
            }, 50);
        },
    }
}
</script>

<style scoped lang="less">
#container {
    overflow-y: scroll;
}
</style>